<template>
  <div class="listmenu">
    <!-- <el-button type="primary" @click="openFullScreen2"> 服务方式 </el-button> -->
    <el-table
      :data="specsGetter"
      style="width: 100%; margin-bottom: 20px"
      row-key="id"
      border
      :tree-props="{ children: 'children', hasChildren: 'hasChildren' }"
    >
      <el-table-column
        prop="id"
        label="规格编号"
        sortable
        width="150"
      ></el-table-column>
      <el-table-column
        prop="specsname"
        label="规格名称"
        sortable
        width="150"
      ></el-table-column>
      <el-table-column label="规格属性" sortable width="200">

        <template slot-scope="scope">
     
          <div>
            <el-tag v-for="item in scope.row.attrs" class="tag"  type="success" :key="item" v-text="item"></el-tag>
          </div>
          
        </template>
      </el-table-column>

      <el-table-column prop="address" label="操作">
        <template slot-scope="scope">
          <div>
            <!-- 这里自定义 表格 在template中 添加 slot-scope = scope -->
            <!-- 判断状态 如果说 后台返回的 status 是 1 就是启用 否则就禁用  -->
            <el-button type="primary" v-if="scope.row.status == 1" plain
              >启用</el-button
            >

            <el-button type="info" v-else plain>禁用</el-button>

            <el-button type="success" @click="edit(scope.row.id)" plain
              >编辑</el-button
            >

            <delbutton @delbtn="del(scope.row.id)"></delbutton>
          </div>
        </template>
      </el-table-column>
    </el-table>

    <!-- 进行分页处理  -->
    <!-- 其中 adminCount的值就是 总共页数 -->
    <el-pagination
      background
      layout="prev, pager, next"
      :page-size="specsPage"
      :total="specsCount"
      @current-change="change"
    >
    </el-pagination>
  </div>
</template>
<script>
// 引入仓库
import { mapGetters, mapActions } from "vuex";
import { reqSpecsDel } from "../../../util/request";
import { successAlert, warningAlert } from "../../../util/alert";

export default {
  computed: {
    ...mapGetters({
      // 菜单请求
      specsGetter: "specs/specsGetter",
      // 获取总数
      specsCount: "specs/specsCount",
      specsPage: "specs/specsPage",
      specsnewpage: "specs/specsnewpage",
    }),
  },

  methods: {
    ...mapActions({
      actionSpecsList: "specs/actionSpecsList",
      actionSpecsCount: "specs/actionSpecsCount",
      actionSpechPage: "specs/actionSpechPage",
    }),

    // 删除函数
    del(id) {
      reqSpecsDel(id).then((res) => {
        if (res.data.code == 200) {
          successAlert(res.data.msg);
          this.actionSpecsList();
          // 删除之后进行判断 页数是不是也要更改
          this.actionSpecsCount();
        } else {
          warningAlert(res.data.msg);
        }
      });
    },
    // 编辑函数
    edit(id) {
      // 给父组建进行传参数
      this.$emit("listedit", id);
    },

    change(page) {
      this.actionSpechPage(page);
      // 重新发送请求
      this.actionSpecsList();

    },
  },

  data() {
    return {
      flag: true,
    };
  },

  // 挂载完成之后 进行 调用 发送请求函数
  mounted() {
    this.actionSpecsList();
    // 调用 接口获取总条数
    this.actionSpecsCount();
  },
};
</script>

<style lang="">
.cell {
  text-align: center;
}

.el-table,
.submit {
  width: 100%;
  margin-top: 100px;
}
.submit {
  font-size: 16px;
}
.el-tag--light{
  margin-left: 10px;
  font-size: 14px;
}

.tag{
  color: #4fc08d;
}

</style>


