<template>
  <div>
    <el-dialog
      :title="isShow.edit ? '添加菜单' : '修改菜单'"
      :visible.sync="isShow.flag"
      @close="cancel"
    >
      <el-form ref="form" label-width="100px">
        <el-form-item label="规格尺寸">
          <el-input v-model="form.specsname"></el-input>
        </el-form-item>

        <el-form-item
          label="规格属性"
          v-for="(item, index) in attrArr"
          :key="index"
        >
          <div class="inpTop">
            <el-input v-model="item.value"></el-input>

            <el-button type="primary" @click="addAttr" v-if="index == 0"
              >新增规格属性</el-button
            >

            <el-button type="danger" @click="delAttr(index)" v-else
              >删除</el-button
            >
          </div>
        </el-form-item>

          <el-form-item label="状态" prop="delivery">
          <!-- active-value="1" :inactive-value = "2" -->
          <!-- 设置 什么值打开 什么值关闭  -->
          <el-switch
            v-model="form.status"
            :active-value="1"
            :inactive-value="2"
          ></el-switch>
        </el-form-item>



      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel()">取 消</el-button>
        <el-button type="primary" v-if="isShow.edit" @click="add()"
          >添 加</el-button
        >
        <!-- 修改按钮 通过 v-if控制  -->
        <el-button type="primary" @click="edit()" v-else>修 改</el-button>
      </div>
    </el-dialog>
  </div>
</template>


<script>
import { mapGetters, mapActions } from "vuex";
import { successAlert, warningAlert } from "../../../util/alert";
import { reqSpecsAdd, reqSpecsInfo, reqSpecsEdit } from "../../../util/request";

export default {
  props: ["isShow"],

  computed: {
    ...mapGetters({
      specsGetter: "specs/specsGetter",
      specsCount: "specs/specsCount",
    }),
  },

  methods: {
    ...mapActions({
      actionSpecsList: "specs/actionSpecsList",
      actionSpecsCount: "specs/actionSpecsCount",
    }),

    // 取消函数
    cancel() {
      this.form = {
        specsname: "",
        attrs: "[]",
        status: 1,
      };

      this.isShow.flag = false;
      this.attrArr = [{ value: ""}];
    },

    // 进行添加函数 发送请求 然后 判断状态码
    add() {
      this.form.attrs = JSON.stringify(
        this.attrArr.map((item) => {
          return item.value;
        })
      );


      reqSpecsAdd(this.form).then((res) => {
        if (res.data.code == 200) {
          // 状态码正确进行 弹窗 清空 以及重新加载
          this.cancel();

          //刷新 页面 以及 引入仓库
          this.actionSpecsList();
          // 用来控制 页数的显示
          this.actionSpecsCount();
          successAlert(res.data.msg);
        } else {
          warningAlert(res.data.msg);
          this.cancel();
        }
      });
    },

    // 进行 获取某一条数据并添加进去
    getOne(id) {
      reqSpecsInfo(id).then((res) => {
        this.form = res.data.list[0];
        console.log(res.data.list[0].attrs);
        this.attrArr = JSON.parse(res.data.list[0].attrs).map(
          (element) => {
            return { value: element };
          }
        );

        this.form.id = id;
      });
    },

    // 进行修改
    edit() {
      // console.log(this.form);
      let list1 = [];

      /*
        * 既然是修改那么 attrArr代表的就是 每个属性 有几个 输入框就有几个
        * 项 attrArr 那么 我们就遍历 这个 东西 获取到所有的 值  加入一个新的 
        * 数组中 然后转换成字符串 进行发送 
        * 
      */ 
      this.attrArr.map((item) => {
        list1.push(item.value);
      });

      this.form.attrs = JSON.stringify(list1);

      reqSpecsEdit(this.form).then((res) => {
        if (res.data.code == 200) {
          successAlert(res.data.msg);
          this.actionSpecsList();
          this.cancel();
        } else {
          warningAlert(res.data.msg);
        }
      });
    },

    addAttr() {
      this.attrArr.push({ value: "" });
    },

    delAttr(index) {
      this.attrArr.splice(index, 1);
    },
  },

  data() {
    return {
      attrArr: [{ value: "" }],
      form: {
        specsname: "",
        attrs: "[]",
        status: 1,
      },
    };
  },

  mounted() {},
};
</script>

<style scoped>
.inpTop {
  display: flex;
}
.inpTop button {
  width: 120px;
  flex: 1;
}
.inpBottom {
  display: flex;
}
.inpBottom button {
  width: 80px;
  flex: 1;
}
</style>


